import React from 'react';
import { TopBar } from './Grid';
import Footer from './Footer';

const About = () =>
  <div>
    <TopBar>About Me</TopBar>
    Over a decade ago, I popped an AOL CD into my Gateway, taught myself HTML, and fell in love with coding websites. In
    the present, I enjoy learning about new technologies and adding to my skill set. After a stint as an educator, I'm
    back to building sites with React, Redux, Webpack and Node.
    <br />
    <br />
    If I were a font, I would definitely be sans-serif. Perhaps I would be Univers: clear, versatile and able to play
    well with others. That said, I'm not afraid to be a organized and technical like Courier.
    <Footer />
  </div>;

export default About;



