import React, { Component } from 'react';
import { Grid as GridContainer, GridSection, TopBar, GridItem } from './Grid';
import PortfolioContent from '../content/portfolio';
import Footer from './Footer';

export default class Portfolio extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  onClick = mappedItem => e => {
    console.log(mappedItem);
    this.props.onClick(mappedItem);
  };
  render() {
    return (
      <GridContainer id="theGrid">
        <GridSection>
          <TopBar>
            Things I Have Done
          </TopBar>
          {PortfolioContent.map((mappedItem, index) => {
            return (
              <GridItem onClick={this.onClick(mappedItem)}>
                <img src={mappedItem.imageUrl} alt="mappedItem.imageUrl" />
                <div className="overlay">
                  <div className="text">
                    <h2>
                      {mappedItem.title}
                    </h2>

                    <p>{mappedItem.shortDescription}</p>
                    <a className="grid__item" href="#">
                      <small>...</small>

                    </a>
                  </div>
                </div>

              </GridItem>
            );
          })}

          <Footer />
        </GridSection>

      </GridContainer>
    );
  }
}



